import React, { useState } from "react";
import styles from "./Card.module.css"; 
import AlertReg from "../AlertReg/AlertReg";
import { useNavigate } from "react-router-dom";

const Card = ({
  name,
  description, 
  format,       
  place,
  duration,
  date,         
  info,
  tags,
  img,
  isLog
}) => {
  const [flagGuest, setFlagGuest] = useState(false);
  const navigate = useNavigate();

  const handleClick = () => {
    if(isLog) {
      const eventData = {
        name,
        description,
        format,
        place,
        duration,
        date,
        info,
        tags,
        img
      };

      const existingEvents = JSON.parse(localStorage.getItem("events")) || [];
      const updatedEvents = [...existingEvents, eventData];
      localStorage.setItem("events", JSON.stringify(updatedEvents));
    }
  };

  const handleFavorite = () => {
    if(isLog) {
      const eventData = {
        name,
        description,
        format,
        place,
        duration,
        date,
        info,
        tags,
        img
      };

      const favoriteEvents = JSON.parse(localStorage.getItem("FavoriteEvents")) || [];
      
      
      const isExist = favoriteEvents.some(event => event.name === name);
      
      if(!isExist) {
        const updatedFavorites = [...favoriteEvents, eventData];
        localStorage.setItem("FavoriteEvents", JSON.stringify(updatedFavorites));
      }
    }
  };

  const handleCloseAlert = () => {
    setFlagGuest(false);
  };

  return (
    <div className={styles.card}>
      <div className={styles.imageContainer}>
        <img 
          src={img} 
          alt=" " 
          className={styles.image}
        />
      </div>
      
      <div className={styles.content}>
        <h3 className={styles.title}>{name}</h3>
        <p className={styles.description}>{description}</p>
        
        <div className={styles.details}>
          <p className={styles.detailItem}>: {format}</p>
          <p className={styles.detailItem}>: {place}</p>
          <p className={styles.detailItem}>: {duration}</p>
          <p className={styles.detailItem}>: {date}</p>
          <p className={styles.detailItem}>: {info}</p>
        </div>

        <div className={styles.tagsContainer}>
          {tags.map((tag, index) => (
            <div key={index} className={styles.tag}>
              {tag}
            </div>
          ))}
        </div>

        <button 
          onClick={handleClick}
          className={styles.participateButton}
        >
          
        </button>
        <button 
          onClick={handleFavorite}
          className={styles.favoriteButton}
        >
            
        </button>
      </div>
    </div>
  );
};

export default Card;